import Vue from 'vue';
import Vuex from 'vuex';
import VueToast from 'vue-toast-notification';
import 'vue-toast-notification/dist/theme-sugar.css';
import user from "./modules/user/store";
import seller from "./modules/seller/store";
import common from './common/store';
import userState from "./modules/user/store/state";
import sellerState from "./modules/seller/store/state";
import commonState from './common/store/state';

Vue.use(Vuex);
Vue.use(VueToast, {
  position: 'bottom-right'
});

const mutations = {
  resetState(state) {
    Object.assign(state, { common: commonState(), user: userState(), seller: sellerState() });
  }
};

const actions = {
  resetState({ commit }) {
    commit('resetState');
  }
};

export default new Vuex.Store({
  actions,
  mutations,
  modules: {
    user,
    seller,
    common
  },
});
