/*
export function someMutation (state) {
}
*/
export function setCurrentRace(state, payload) {
    if (typeof payload === 'object') {
        payload = Object.entries(payload).map(a => a[1]);
    }
    state.currentRace = [ ...payload ];
}
export function setCurrentRaceUserInfo(state, payload) {
    if(payload.race)delete payload.race;
    if(payload.password)delete payload.password;
    state.currentRaceUserInfo = {...payload };
}

