export default [
    {
        path: '/user',
        component: () => import('./layouts/UserLayout.vue'),
        children: [
            {
                path: 'selection/:idx',
                component: ()=>import('./views/UserSelectView.vue')
            },
            {
                path: '',
                name:'user',
                component: ()=>import('./views/UserAllQuizView.vue')
            }
        ]
    }
];