import Vue from 'vue';
import Router from 'vue-router';
import app from './main';
import commonRoutes from '@/common/routes';
import userRoutes from '@/modules/user/routes';
import sellerRoutes from '@/modules/seller/routes';
Vue.use(Router);

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        ...userRoutes,
        ...sellerRoutes,
        ...commonRoutes,
    ]
});

router.beforeEach((to, from, next) => {
    if (to.fullPath === '/') return next();
    let currentUser = localStorage.getItem('currentUser');
    if (currentUser) currentUser = JSON.parse(currentUser);
    console.log(currentUser);
    console.log(to);
    if (to.fullPath.startsWith('/race-done') && (currentUser && currentUser.raceDone)) {
        console.log('stay here');
        return next();
    }
    if (!to.fullPath.startsWith('/race-done') && (currentUser && currentUser.raceDone)) {
        console.log('redirect to race-done');
        return next({ path: '/race-done' });
    } else if (!to.fullPath.startsWith('/login') && !currentUser) {
        console.log('redirect to login');
        return next({ path: '/login' });
    }
    else if (!to.fullPath.startsWith('/user') && currentUser && currentUser.type === 'user') {
        console.log('redirect to user');
        return next({ name: 'user' });
    }
    else if (!to.fullPath.startsWith('/seller') && currentUser && currentUser.type === 'seller' && currentUser.raceUser) {
        return next({ name: 'seller' });
    }
    if (currentUser && currentUser.type === 'seller' && !currentUser.raceUser && !to.fullPath.startsWith('/select-race')) {
        console.log('no race detected');
        console.log('redirect to select-race');
        return next({ path: '/select-race' });
    } else if (currentUser && currentUser.type === 'seller' && currentUser.raceUser && to.fullPath.startsWith('/select-race')) {
        console.log('redirect to seller');
        return next({ name: 'seller' });
    }
    else return next();
});

export default router;